import {mapState} from 'vuex'
import Keyboard from '../components/keyboard'
export default {
  name: 'home',
  components: {Keyboard},
  inject: ['reload'], // 引入方法
  data () {
    return {
      putting: false,
      electric: false,
      licenceViewVisible: false,
      licence: '',
      querying: false
    }
  },
  computed: {
    ...mapState({
    }),
    queryActive: function () {
      if (this.licence.length === 8 || this.licence.length === 7) {
        return true
      }
      return false
    }
  },
  watch: {
  },
  mounted () {
  },
  destroyed () {
  },
  methods: {
    switchElectric () {
      this.electric = true
      this.showKeyboard()
    },
    licenceCompleted (licence) {
      this.licence = licence
    },
    hideKeyboard () {
      this.putting = false
      this.licenceViewVisible = false
    },
    showKeyboard () {
      this.putting = true
      this.licenceViewVisible = true
    },
    licenceFinished () {
      this.putting = false
      this.licenceViewVisible = false
    },
    doQuery () {
      if (this.querying) {
        return false
      }
      if (this.licence.length !== 8 && this.licence.length !== 7) {
        return false
      }
      this.$vux.loading.show({
        text: '查询中'
      })
      this.querying = true

      setTimeout(() => {
        this.$vux.loading.hide()
        this.querying = false
        // this.$vux.toast.show({
        //   type: 'text',
        //   text: '未查询到您的停车订单',
        //   width: '200px',
        //   position: 'middle'
        // })
        this.$router.push('/pay')
      }, 2000)
    }
  }
}



